<template>
    <div>
        <!-- 头部 -->
        <div class="header">
            <a href="">
                <i class="iconfont icon-fanhui"></i>
            </a>
            <div class="header-word">购物车</div>
            <div class="kong">编辑</div>
        </div>
        <!-- 购物车界面开始 -->
        <div class="buycar" v-if="cart != null">
            <!-- 购物车顶部开始 -->
            <div class="head"></div>
            <!-- 购物车顶部结束 -->
            <!-- 定位收货时间开始 -->
            <div class="position">
                小黄蜂需要您的精确坐标
                <i class="iconfont icon-you"></i>
            </div>
            <div class="shop">
                收货时间
                <i class="iconfont icon-you"></i>
                <span>今天30分钟可送达(可预订)</span>
            </div>
            <!-- 定位收货时间结束 -->
            <!-- 购物车订单来袭 -->
            <div class="buycar-content">
                <div class="buycar-item" v-for="item in cart.goods" :key="item.gid">
                    <div class="btn">
                        <input type="checkbox" name="sub2" class="shopcart-input1 btn2" :checked="item.status==1?checked:0" @click="toggleGoodsStatus(item)">
                    </div>
                    <div class="item-img">
                        <img :src="imgHostname+item.gthumb" alt="蛋糕">
                    </div>
                    <div class="item-content">
                        <span>{{item.gname}}</span>
                        <div class="item-money">
                            ￥<span class="dj">{{item.sale}}</span>
                        </div>
                        <div class="item-number">
                            <div class="btn-reduce" @click="reduceGood(item)"></div>
                            <div style="float: left;"><span class="num">{{$store.getters.goodsNumber(item.gid)}}</span></div>
                            <div class="btn-add" @click="addGood(item)"></div>
                        </div>
                    </div>
                </div>
                <div style="height: 2rem"></div>
            </div>
            <!-- 购物车订单结束 -->
            <!-- 购物车合计开始 -->
            <div class="buycar-total">
                <div class="btn-all">
                    <div class="btnAll">
                        <input type="checkbox" id="ckAll" :checked="$store.getters.isFullSelected?checked:0" @click="changeStatus">
                    </div>
                    <span class="btnall">全选</span>
                </div>
                <span class="total-content">合计：￥</span>
                <span id="allmoney" class="total-money">{{$store.getters.cartPrice}}</span>
                <div  class="Settlement" :class="{active:existSelectedGoods}" @click="settlement">去结算</div>
                <div class="iconlajitong">
                    <i class="iconfont icon-lajitong"></i>
                </div>
            </div>
            <!-- 购物车合计结束 -->
        </div>
        <div v-if="cart == null"><h1>暂无更多商品数据</h1></div>
        <!-- 购物车界面结束 -->
        <my-footer :active="2"></my-footer>
    </div>
</template>

<script>
    import Vue from 'vue'
    import MyFooter from '../../components/my-footer/my-footer'
    import {IMGHOSTNAME} from "../../config/base";
    import {mapState,mapGetters} from 'vuex';
    import {addGoods,reduceGoods,allStatus,insertOrders} from "../../api/cart";
    import {SUCCESS} from "../../config/base";
    import{toggleGoodsStatus} from "../../api/cart";
    import {Toast} from 'vant'
    Vue.use(Toast);
    export default {
        name: "Cart",
        components:{
          MyFooter,
        },
        computed:{
          ...mapState({
              cart:'cart',
          }),
            ...mapGetters([
                'isFullSelected','existSelectedGoods'
            ])
        },

        data(){
            return {
                imgHostname:IMGHOSTNAME,
                checked:'checked',
                status:0,
            }
        },
        methods:{
            //加上一个
            addGood(item){
                let{gid,sale,gname,gthumb,num}=item;
                addGoods('/api/cart',{gid,sale}).then(res=>{
                    if (res.code == SUCCESS){
                        // this.$store.commit('incGoods',gid);
                        // this.$store.getters.goodsNumber(gid);
                        let {uid,cid} = res.data;
                        this.$store.commit('incGoods',{uid,cid,gid,sale,gname,gthumb,num});
                        this.$store.commit('calcTotal');
                        this.$store.commit('calcPrice');
                    }
                });
            },
            //减去一个
            reduceGood(item){
                let{gid,sale,gname,gthumb,num}=item;
                reduceGoods('/api/cartreduce',{gid,sale}).then(res=>{
                    if (res.code == SUCCESS){
                        // this.$store.commit('incGoods',gid);
                        // this.$store.getters.goodsNumber(gid);
                        let {uid,cid} = res.data;
                        this.$store.commit('decGoods',{uid,cid,gid,sale,gname,gthumb,num});
                        this.$store.commit('calcTotal');
                        this.$store.commit('calcPrice');
                    }
                });
            },
            //商品选中状态
            toggleGoodsStatus(goods){
                let {gid,sale}=goods;
                toggleGoodsStatus('/api/cart/12',{gid,sale}).then(res=>{
                    if (res.code == SUCCESS){
                        this.$store.commit('togggleGoodsStatus',{gid});
                        this.$store.commit('calcTotal');
                        this.$store.commit('calcPrice');
                    }
                })
            },
            //去结算
            settlement(){
                if (!this.existSelectedGoods){
                    Toast('请至少选择一件商品');
                    return;
                }
                insertOrders('/api/orders').then(res=>{
                    if (res.code == SUCCESS){
                        this.$store.commit('ordersFinish');
                        this.$router.push({name:'orders',query:{ordernum:res.ordernum}});
                    }
                })
            },
            //全选改变商品选中状态
            changeStatus(){
                let statusgoods =this.$store.state.cart.goods.filter(ele=>ele.status == 1).length;
                let goodslength = this.$store.state.cart.goods.length;
                if (statusgoods < goodslength){
                        this.status = 1;
                }else if (statusgoods == goodslength) {
                        this.status = 0;
                }
                let {status}=this;
                allStatus('/api/status',{status}).then(res=> {
                    if(res.code == SUCCESS){
                        this.$store.commit('allStatus',{status});
                        this.$store.commit('calcTotal');//获取总数量
                        this.$store.commit('calcPrice');//获取总价格
                    }
                })
            }
        }

    }
</script>

<style scoped>
@import "../../style/css/buycar.css";
    chaochushenglve{
        height: 0.96rem;
        font-size: 0.032rem;
        line-height: 1.5;
        color: #333;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
    }
    .active{
        background: #ff9201;
    }
</style>
